html, body {
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased; }

.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%; }

.content {
  flex: 1 0 auto; }

footer {
  flex: 0 0 auto; }

.container {
  width: 940px;
  max-width: 90%;
  margin: 0 auto;
  display: flex;
  justify-content: center; }

#messages {
  position: fixed;
  top: 65px;
  right: 25px;
  z-index: 1;
  width: 25%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.message-alert {
  width: 100%;
  margin: 5px 0;
  padding: 25px 10px;
  z-index: 1;
  border-radius: 5px;
  text-align: center;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: relative; }
  .message-alert span {
    color: #232323;
    font-family: Lato, sans-serif; }
  .message-alert p {
    margin: 5px; }

.errors-block {
  justify-content: flex-start;
  flex-direction: column;
  padding: 10px; }

.errors-list {
  align-self: flex-start;
  margin: 0;
  padding: 0 0 0 50px; }

.alert-error {
  background-color: lightcoral; }

.alert-success {
  background-color: lightgreen; }

.tooltip {
  position: relative;
  display: inline-block;
  border-bottom: 1px dotted black; }

.tooltip .tooltiptext {
  visibility: hidden;
  width: 120px;
  background-color: black;
  color: #fff;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 100%;
  left: 50%;
  margin-left: -60px;
  /* Fade in tooltip - takes 1 second to go from 0% to 100% opac: */
  opacity: 0;
  transition: opacity 1s; }

.tooltip .tooltiptext::after {
  content: " ";
  position: absolute;
  top: 100%;
  /* At the bottom of the tooltip */
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: black transparent transparent transparent; }

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1; }

#popupEdit, #popupAdd {
  position: fixed;
  display: none;
  overflow-y: scroll;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.25); }

.popup-content {
  overflow-y: auto;
  width: 70%;
  min-height: 500px;
  height: 90%;
  margin: 30px auto;
  flex-direction: column;
  align-items: center;
  background-color: white;
  border-radius: 10px;
  box-shadow: 0 1px 6px rgba(32, 33, 36, 0.28);
  position: relative; }
  .popup-content h1 {
    text-align: center; }

.popup-close {
  font-size: 2em;
  position: absolute;
  top: 20px;
  right: 20px;
  height: 30px;
  width: 30px;
  cursor: pointer;
  color: #232323; }

.popup-form {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-bottom: 50px; }

.popup-form-row, .popup-form-button {
  width: 50%;
  display: flex;
  flex-direction: row;
  align-items: center; }

.popup-form-row {
  min-height: 50px; }
  .popup-form-row input, .popup-form-row label, .popup-form-row select, .popup-form-row textarea {
    padding: 4px 8px;
    color: #0b2e13; }
  .popup-form-row label {
    flex: 2 0 0; }
  .popup-form-row input, .popup-form-row select, .popup-form-row textarea {
    flex: 4 0 0;
    border: none;
    border-bottom: 1px solid #d3d3d3; }

.popup-form-button {
  margin-top: 20px; }
  .popup-form-button button {
    cursor: pointer;
    background-color: #2196f3;
    color: white;
    border-radius: 8px;
    padding: 8px 12px; }

.required {
  position: relative; }
  .required:after {
    position: absolute;
    right: -10px;
    top: 0;
    content: '*';
    color: #cc2525; }

.color-green {
  color: green; }

.cursor-pointer {
  cursor: pointer; }
